@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.container {
  /* position: relative; */
  width: 350px;
  height: 500px;
  border-radius: 20px;
  /* padding: 40px; */
  /* box-sizing: border-box; */
  background: #ecf0f3;
  box-shadow: 14px 14px 20px #cbced1, -14px -14px 20px white;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  padding-top: 21px;
}

@media screen and (max-width: 540px) {
  .container {
    width: 100%;
  }
}

.brand-logo {
  height: 100px;
  width: 100px;
  margin: auto;
  border-radius: 50%;
  box-sizing: border-box;
  box-shadow: 7px 7px 10px #cbced1, -7px -7px 10px white;
}

.input-login1 {
  background: #ecf0f3;
  padding: 10px;
  /* padding-left: 20px; */
  height: 20px;
  font-size: 14px;
  border-radius: 50px;
  box-shadow: inset 6px 6px 6px #cbced1, inset -6px -6px 6px white;
  width: 176px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.input-login2 {
  background: #ecf0f3;
  padding: 10px;
  /* padding-left: 20px; */
  height: 20px;
  font-size: 14px;
  border-radius: 50px;
  box-shadow: inset 6px 6px 6px #cbced1, inset -6px -6px 6px white;
  width: 176px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.inputs {
  text-align: center;
  margin-top: 30px;
}

.email-text,
.password-text {
  text-align: center;
  margin-bottom: 5px;
  margin-top: 5px;
}

.btn-login {
  margin-top: 20px;
  background: #2fc18c;
  height: 40px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: 900;
  box-shadow: 6px 6px 6px #cbced1, -6px -6px 6px white;
  transition: 0.5s;
  border: none;
  outline: none;
  width: 170px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-left: auto;
  text-align: center;
  margin-right: auto;
  align-items: center;
}

label {
  margin-bottom: 20px;
}

.brand-title {
  margin-top: 10px;
  font-weight: 900;
  font-size: 1.8rem;
  color: #2fc18c;
  letter-spacing: 1px;
  text-align: center;
}

.link {
  font-size: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

body {
  margin: 0;
  padding: 20px;
}

.tasks1 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #036b52;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
  /* margin: 10px; */
  border-radius: 10px;
}

.parag-email {
  margin-right: auto;
  margin-left: 5px;
}

.perfil {
  display: flex;
}

.total-expense {
  margin-right: 10px;
}

.name-trybe {
  /* margin-right: 450px; */
  align-items: center;
  display: flex;
  justify-content: center;
  font-weight: 900;
  font-size: 18px;
  color: #2fc18c;
  letter-spacing: 1px;
  margin-left: 30px;
  margin-right: 10px;
}

#btn {
  margin-top: 1px;
  margin-right: 5px;
  background: #2fc18c;
  border-radius: 20px;
  cursor: pointer;
  font-weight: 200;
  transition: 0.5s;
  border: none;
  outline: none;
  width: 150px;
  height: 20px;
}

form {
  background-color: rgb(207, 206, 206);
  padding-top: 10px;
  padding-bottom: 10px;
  /* margin: 10px; */
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
}

#value {
  margin-left: 5px;
  margin-right: 10px;
}

#description {
  margin-left: 5px;
  margin-right: 10px;
}

label {
  margin-right: 40px;
  margin-left: 10px;
}

#currency {
  margin-left: 5px;
  margin-right: 10px;
}

#method {
  margin-left: 5px;
  margin-right: 10px;
}

#tag {
  margin-left: 5px;
  margin-right: 10px;
}

th {
  width: 200px;
  border-collapse: collapse;
  font-size: 14px;
  padding-bottom: 5px;
  padding-top: 5px;
}

td {
  text-align: center;
  /* border-collapse: collapse; */
  margin: none;
  font-size: 13px;
  /* margin: 10px;s */
}

#pic {
  border-radius: 50%;
  margin-top: 7px;
  margin-left: 5px;
}

.edit-th {
  border-right: 10px;
}

.edit-button {
  margin-right: 20px;
}

.form-table {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 10px;
  flex-wrap: wrap;
}

@media screen and (max-width: 678px) {
  .despesas {
    margin-left: auto;
  }
}

.table-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.table-table td,
.table-table th {
  padding: 12px 15px;
  border: 1px solid #ddd;
  text-align: center;
  padding: 20px;
}

.table-table th {
  background-color: rgb(176, 232, 176);
}

.table-table tbody tr:nth-child(even) {
  background-color: #f5f5f5;
}

@media screen and (max-width: 1001px) {
  .table-table #table-th {
    display: none;
  }

  .table-table,
  .table-table tbody,
  .table-table tr,
  .table-table td {
    display: block;
    /* width: 100%; */
  }

  .table-table tr {
    margin-bottom: 15px;
  }

  .table-table td {
    text-align: right;
    padding-left: 50%;
    text-align: right;
    position: relative;
    height: 15px;
  }

  .table-table td::before {
    content: attr(aria-label);
    position: absolute;
    left: 0;
    width: 50%;
    padding-left: 15px;
    font-size: 15px;
    font-weight: bold;
    text-align: left;
  }
}

@media screen and (min-width: 1002px) {
  .icons {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
}

@media screen and (max-width: 582px) {
  .form-table {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: self-start;
  }

  .form-table label {
    padding-left: 47px;
  }
  #btn {
    margin-left: auto;
    margin-right: auto;
  }
}

